<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe624;</div>
    </div>
    <div class="header-input">
      <span class="iconfont">&#xe632;</span>
      输入城市/游玩/主题
    </div>
    <router-link to="/city">
    <div class="header-right" >{{this.city}}
      <span class="iconfont ">&#xe6aa;</span>
    </div>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'Header',
  props: {
    city: String
  }
}
</script>
<!--使用stylus，并且只对这个组件有效-->
<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .header
    line-height :.86rem
    display flex
    background-color $bgColor
    color #ffffff
    .header-left
      width :.64rem
      float :left
      .back-icon
        font-size .4rem
        text-align center
    .header-input
      height .64rem
      line-height .64rem
      margin-top .12rem
      padding-left .2rem
      color #cacaca
      margin-left .2rem
      background #ffffff
      border-radius .12rem
      flex :1
    .header-right
      min-width: 1.04rem
      padding: 0 .1rem
      float: right
      text-align: center
      color: #fff
</style>



